*{
    list-style: none;
    text-decoration: none;
}
.sidebar{
    display: flex;
    width: 17%;
    background: var(--white);
    border-radius: 20px;
    margin: 30px 0 ;
    height: max-content;
    position: fixed;
}
.side{
    width: 100%;
}
.sidebar h2{
    font-size: 14px;
}
.sidetop{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 20px;
    padding: 10px;
    text-align: left;
}
.img2{
    background-image: url(../../../public/avatar3.jpg);
    background-size: cover;
    width: 50px;
    height: 50px;
    border-radius: 50%;
}
.links{
    display: flex;
    flex-direction: column;
    gap: 10px;
}
.links a{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 15px;
    padding: 10px ;
    color: var(--black);
}
.links a:hover {
    background: var(--background);
    color: var(--blue);
}
.links a i{
    font-size: 20px;
}
.btn{
    background: var(--background);
    padding: 10px;
}
.btn1{
    width: 100%;
    padding: 20px;
}
@media screen and (max-width: 800px) {
    .sidebar{
        top:32px;
        left:0;
        width: 100%;
        height: 6%;
        z-index: 3;
    }
    .side{
        display: flex;
    }
    .links{
        flex-direction: row;
        align-items: center;
        justify-content: center;
        gap:30px;
        width: 100%;
        background: var(--white);
    }
    .links h2{
        display: none;
    }
    .links a i{
        font-size: 25px;
        color: var(--blue)
    }
    .sidetop{
        background: var(--white);
        display: none;
    }
    .sidetop h2, p {
        display: none;
    }
    .btn {
        display: none;
    }
    .sidetop{
        padding: 0 0 0 10px;
        margin: 0 auto;
    }
    
}
